@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;700;800&display=swap");

:root {
  --card-width: 200px;
  --card-height: 300px;
  --card-transition-duration: 800ms;
  --card-transition-easing: ease;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.themeSlider {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgb(63, 63, 63);
  overflow: hidden;
}

button {
  border: none;
  background: none;
  cursor: pointer;
}

button:focus {
  outline: none;
  border: none;
}

.app {
  position: relative;
  width: 150%;
  height: 100%;
  left: -25%;
  display: flex;
  justify-content: center;
  align-items: center;

}


.apps__bg {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -5;
  margin-left: 0px;
  filter: blur(8px);
  pointer-events: none;
  user-select: none;
  overflow: hidden;
}

.apps__bg::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  /* background: #ffffff; */
  z-index: 1;
  opacity: 0.8;
}

.apps__bg__image {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) translateX(var(--image-translate-offset, 0));
  width: 180%;
  height: 180%;
  transition: transform 1000ms ease, opacity 1000ms ease;
  overflow: hidden;
}

.apps__bg__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.apps__bg__image.first--image {
  opacity: 1;
  --image-translate-offset: 0;
}

.apps__bg__image.third--image,
.apps__bg__image.second--image,
.apps__bg__image.fourth--image,
.apps__bg__image.fifth--image {
  opacity: 0;
}

.apps__bg__image.third--image {
  --image-translate-offset: -25%;
}

.apps__bg__image.second--image {
  --image-translate-offset: 25%;
}

.apps__bg__image.fourth--image {
  --image-translate-offset: 25%;
}

.apps__bg__image.fifth--image {
  --image-translate-offset: -25%;
}

.cardsList {
  position: absolute;
  width: calc(3 * var(--card-width));
  height: auto;

}

.cardsList__btn {
  --btn-size: 55px;
  width: var(--btn-size);
  height: var(--btn-size);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 100;
}

.cardsList__btn.btn--left {
  left: -5%;
}

.cardsList__btn.btn--right {
  right: -5%;
}

.cardsList__btn .icon {
  width: 100%;
  height: 100%;
}

.cardsList__btn .icon svg {
  width: 100%;
  height: 100%;

}


.cardsList .cards__wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  perspective: 1000px;
}

.cardTheme {
  --card-translateY-offset: 100vh;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) translateX(var(--card-translateX-offset)) translateY(var(--card-translateY-offset)) rotateY(var(--card-rotation-offset)) scale(var(--card-scale-offset));
  display: inline-block;
  width: var(--card-width);
  height: var(--card-height);
  transition: transform var(--card-transition-duration) var(--card-transition-easing);
  user-select: none;
}

.cardTheme::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #000;
  z-index: 1;
  transition: opacity var(--card-transition-duration) var(--card-transition-easing);
  opacity: calc(1 - var(--opacity));
}

.card__image {
  position: relative;
  width: 100%;
  height: 100%;
}

.card__image img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.cardTheme.first--card {
  --first-card-rotation-offset: 0;
  --card-translateX-offset: 0;
  --card-rotation-offset: var(--first-card-rotation-offset);
  --card-scale-offset: 1.2;
  --opacity: 0.8;
}

.cardTheme.third--card {
  --card-translateX-offset: calc(-1 * var(--card-width) * 1.1);
  --card-rotation-offset: 25deg;
}

.cardTheme.second--card {
  --card-translateX-offset: calc(var(--card-width) * 1.1);
  --card-rotation-offset: -25deg;
}

.cardTheme.fifth--card {
  --card-translateX-offset: calc(-2 * var(--card-width) * 1.1);
  --card-rotation-offset: 35deg;
}

.cardTheme.fourth--card {
  --card-translateX-offset: calc(2 * var(--card-width) * 1.1);
  --card-rotation-offset: -35deg;
}

.cardTheme.third--card,
.cardTheme.second--card,
.cardTheme.fourth--card,
.cardTheme.fifth--card {
  --card-scale-offset: 0.9;
  --opacity: 0.4;
}

.infoList {
  position: absolute;
  width: calc(3 * var(--card-width));
  height: var(--card-height);
  pointer-events: none;
}

.infoList .info__wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  perspective: 1000px;
  transform-style: preserve-3d;
}

.info {
  margin-bottom: calc(var(--card-height) / 8);
  margin-left: calc(var(--card-width) / 1.5);
  transform: translateZ(2rem);
  transition: transform var(--card-transition-duration) var(--card-transition-easing);
  background-color: #0000005c;
  padding: 30px 10px;
  border-radius: 10px;

}

.info .text {
  position: relative;
  font-family: "Montserrat";
  font-size: calc(var(--card-width) * var(--text-size-offset, 0.2));
  /* white-space: nowrap; */
  color: #fff;
  width: fit-content;

}

.info .name,
.info .location {
  text-transform: uppercase;
}

.info .location {
  font-weight: 800;
}

.info .location {
  --mg-left: 40px;
  --text-size-offset: 0.12;
  font-weight: 600;
  margin-left: var(--mg-left);
  margin-bottom: calc(var(--mg-left) / 2);
  padding-bottom: 0.8rem;
}

.info .location::before,
.info .location::after {
  /* content: ""; */
  position: absolute;
  /* background: #fff; */
  left: 0%;
  transform: translate(calc(-1 * var(--mg-left)), -50%);
}

.info .location::before {
  top: 50%;
  width: 20px;
  height: 5px;
}

.info .location::after {
  bottom: 0;
  width: 60px;
  height: 2px;
}

.info .description {
  --text-size-offset: 0.065;
  font-weight: 500;
  word-wrap: normal;
}

.info.first--info {
  opacity: 1;
  display: block;
}

.info.third--info,
.info.second--info,
.info.fourth--info,
.info.fifth--info {
  opacity: 0;
  display: none;
}

.loading__wrapper {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #000;
  z-index: 200;
}

.loading__wrapper .loader--text {
  color: #fff;
  font-family: "Montserrat";
  font-weight: 500;
  margin-bottom: 1.4rem;
}

.loading__wrapper .loader {
  position: relative;
  width: 200px;
  height: 2px;
  background: rgba(255, 255, 255, 0.25);
}

.loading__wrapper .loader span {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #f00;
  transform: scaleX(0);
  transform-origin: left;
}

@media only screen and (min-width: 800px) {
  :root {
    --card-width: 250px;
    --card-height: 400px;
  }

}

.support {
  position: absolute;
  right: 10px;
  bottom: 10px;
  padding: 10px;
  display: flex;
}

.support a {
  margin: 0 10px;
  color: #fff;
  font-size: 1.8rem;
  backface-visibility: hidden;
  transition: all 150ms ease;
}

.support a:hover {
  transform: scale(1.1);
}



@media screen and (max-width: 680px) {
  .cardsList {
    width: auto; 
  }

  .cardsList__btn  {
    margin-left: -153px;
    margin-right: -153px;
}

  svg {
    z-index: 8;
  }

  
.infoList {
  position: absolute;
  width: 70%;
  height: var(--card-height);
  pointer-events: none;
  margin-right: 5px;
  text-align: justify;
}

